/* loader css and  animation */
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid rgb(32, 49, 65); /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
